        { id: 2, name: 'Heavenly majesty', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/636/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'},
        { id: 3, name: 'The Gracious Lord of Waters', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/c65/223_228_1d7a58ff99b324185ccb5ad5dfbdb5e85/Blagodatnyy-vladyka-vod.png'},
        { id: 4, name: 'Desecrated desire', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/1f1/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'},
        { id: 5, name: 'The memory of dust', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/511/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'},
        { id: 6, name: 'Montenegrin agate', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/088/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'},
        { id: 7, name: 'Deadly battle', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/67a/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'},
        { id: 8, name: 'Aqua Simulacrum', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/e55/256_254_1d7a58ff99b324185ccb5ad5dfbdb5e85/Akva-simulyakrum.png'},
        { id: 9, name: 'Desiccant', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/2e7/a0ndnp84kb6mp0gyn10naco9phqqty8b/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/Issushitel.webp'},
        { id: 10, name: 'The Heavenly Sword', imagePath:'https://genshin-info.ru/upload/resize_cache/iblock/882/256_256_1d7a58ff99b324185ccb5ad5dfbdb5e85/tmp256.png'}
      ]
    };
  },
  created() {
    Promise.all([
      this.fetchData(this.$BaseURL + 'characters', 'characters'),
      this.fetchData(this.$BaseURL + 'weapons', 'weapons')
    ]).then(results => {
      const [charactersData, weaponsData] = results;
      this.characters = charactersData;
      this.weapons = weaponsData;
    }).catch(error => {
      console.log(error);
    });
  },
  methods: {
    fetchData(url, target) {
      return new Promise((resolve, reject) => {
        this.$axios({
          method: 'get',
          url: url,
        }).then(response => {
          if (target === 'characters') {
            const updatedCharacters = response.data.map(character => {
              const foundCharacter = this.genshinCharacters.find(char => char.name === character.name);
              if (foundCharacter) {
                character.imagePath = foundCharacter.imagePath;
              }
              return character;
            });
            resolve(updatedCharacters);
          } else if (target === 'weapons') {
            const updatedWeapons = response.data.map(weapon => {
              console.log(weapon.name, weapon.level)
              const foundWeapon = this.genshinWeapon.find(weap => weap.name === weapon.name);
              if (foundWeapon) {
                weapon.imagePath = foundWeapon.imagePath;
              }
              return weapon;
            });
            resolve(updatedWeapons);
          }
        }).catch(error => {
          reject(error);
        });
      });
    },
    addCharacter() {
      if (this.newCharacter.name) {
        const isCharacterAlreadySelected = this.characters.some(char => char.name === this.newCharacter.name);
        if (!isCharacterAlreadySelected) {
          const selectedCharacter = this.genshinCharacters.find(char => char.name === this.newCharacter.name);
          if (selectedCharacter) {
            this.characters.push({
              name: this.newCharacter.name,
              level: this.newCharacter.level,
              attackLevel: this.newCharacter.attackLevel,
              elementalSkillLevel: this.newCharacter.elementalSkillLevel,
              elementalBurstLevel: this.newCharacter.elementalBurstLevel,
              imagePath: selectedCharacter.imagePath,
              levelUpDays: this.newCharacter.levelUpDays,
            });
            console.log(this.newCharacter.name, this.newCharacter.attackLevel, this.newCharacter.elementalSkillLevel, this.newCharacter.elementalBurstLevel);
            this.addPoint();
            this.resetForm();
            this.toggleForm();
          } else {
            console.log('Выбранный персонаж не найден');
          }
        } else {
          console.log('Этот персонаж уже выбран');
          alert('Этот персонаж уже выбран');
        }
      }
    },
    addWeapon() {
      if (this.newWeapon.name) {
        const isWeaponAlreadySelected = this.weapons.some(char => char.name === this.newWeapon.name);
        if (!isWeaponAlreadySelected) {
          const selectedWeapon = this.genshinWeapon.find(char => char.name === this.newWeapon.name);
          if (selectedWeapon) {
            this.weapons.push({
              name: this.newWeapon.name,
              level: this.newWeapon.level,
              typeOfWeapon: this.newWeapon.typeOfWeapon,
              imagePath: selectedWeapon.imagePath
            });
            console.log(this.newWeapon.name, this.newWeapon.level, this.newWeapon.typeOfWeapon, this.newWeapon.imagePath);
            this.addPoint_weapon();
            this.resetForm_weapon();
            this.toggleForm_weapon(); // Скрыть форму после добавления
          } else {
            console.log('Выбранное оружие не найдено');
          }
        } else {
          console.log('Это оружие уже выбрано');
          alert('Это оружие уже выбрано');
        }
      }
    },
    resetForm() {
      this.newCharacter = {
        name: '',
        Level: 1,
        attackLevel: 1,
        elementalSkillLevel: 1,
        elementalBurstLevel: 1,
        levelUpDays: ''
      };
    },
    resetForm_weapon() {
      this.newWeapon = {
        name: '',
        level: 1,
      };
    },
    toggleForm() {
      this.showForm = !this.showForm;
    },
    toggleForm_weapon() {
      this.showForm_weapon = !this.showForm_weapon;
    },
    increaseLevel(character, levelType) {
      if (character[levelType] < 90) {
        character[levelType]++;
        this.putPointRequest(character.name, character.Level, character.attackLevel, character.elementalSkillLevel, character.elementalBurstLevel, character.levelUpDays, character.imagePath)
      }
    },
    increaseLevel_weapon(weapon, levelType) {
      if (weapon[levelType] < 90) {
        weapon[levelType]++;
        this.putPointRequest_weapon(weapon.name, weapon.level, weapon.typeOfWeapon, weapon.imagePath)
      }
    },
    decreaseLevel(character, levelType) {
      if (character[levelType] > 1) {
        character[levelType]--;
        this.putPointRequest(character.name, character.Level, character.attackLevel, character.elementalSkillLevel, character.elementalBurstLevel, character.levelUpDays, character.imagePath)
      }
    },
    decreaseLevel_weapon(weapon, levelType) {
      if (weapon[levelType] > 1) {
        weapon[levelType]--;
        this.putPointRequest_weapon(weapon.name, weapon.level, weapon.typeOfWeapon, weapon.imagePath)
      }
    },
    toggleFavorite(character) {
      character.favorite = !character.favorite;

      if (character.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.unshift(character);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.push(character);
        }
      }
    },
    toggleFavorite_weapon(weapon) {
      weapon.favorite = !weapon.favorite;

      if (weapon.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.weapons.indexOf(weapon);
        if (index !== -1) {
          this.weapons.splice(index, 1);
          this.weapons.unshift(weapon);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.weapons.indexOf(weapon);
        if (index !== -1) {
          this.weapons.splice(index, 1);
          this.weapons.push(weapon);
        }
      }
    },
    logoutRequest: function () {
      this.$axios({
        method: 'post',
        url: this.$BaseURL + 'logout',
      }).then(response => {
        this.response = response;
        localStorage.removeItem('currentUser');
        this.$router.push('/');
      }).catch(error => {
        console.log(error);
      });
    },
    addPointRequest: function (name, level, attackLevel, elementalSkillLevel, elementalBurstLevel, levelUpDays, imagePath) {
      let base64Credential = localStorage.getItem('currentUser');
      let username = localStorage.getItem('username');
      console.log(name, level, attackLevel, elementalSkillLevel, elementalBurstLevel, imagePath);
      return this.$axios({
        method: 'post',
        url: this.$BaseURL + 'characters',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, level: level, attackLevel: attackLevel, elementalSkillLevel: elementalSkillLevel, elementalBurstLevel: elementalBurstLevel, levelUpDays: levelUpDays, imagePath: imagePath }
      }).then(response => {

        let result = response.data.result;
        const existingCharacterIndex = this.characters.findIndex(char => char.name === name);
        const selectedCharacter = this.genshinCharacters.find(char => char.name === name); // Находим соответствующий объект-персонаж
        if (existingCharacterIndex !== -1) {
          // Update existing character if found
          this.$set(this.characters, existingCharacterIndex, {name, Level, attackLevel, elementalSkillLevel, elementalBurstLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' , username});
          console.log(this.characters)
          // localStorage.setItem('characters', JSON.stringify(this.characters));
        } else {
          this.characters.unshift({name, level, attackLevel, elementalSkillLevel, elementalBurstLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' });
        }
        this.response = response;
        console.log(response);

      }).catch(error => {
        console.log(error.response.data);
        return false;
      });
    },
    putPointRequest: function (name, level, attackLevel, elementalSkillLevel, elementalBurstLevel, levelUpDays, imagePath) {
      let base64Credential = localStorage.getItem('currentUser');
      let username = localStorage.getItem('username');
      console.log(name, level, attackLevel, elementalSkillLevel, elementalBurstLevel, imagePath);
      return this.$axios({
        method: 'put',
        url: this.$BaseURL + 'characters',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, level: level, attackLevel: attackLevel, elementalSkillLevel: elementalSkillLevel, elementalBurstLevel: elementalBurstLevel, levelUpDays: levelUpDays, imagePath: imagePath }
      }).then(response => {

        let result = response.data.result;
        const existingCharacterIndex = this.characters.findIndex(char => char.name === name);
        const selectedCharacter = this.genshinCharacters.find(char => char.name === name); // Находим соответствующий объект-персонаж
        if (existingCharacterIndex !== -1) {
          // Update existing character if found
          this.$set(this.characters, existingCharacterIndex, {name, Level, attackLevel, elementalSkillLevel, elementalBurstLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' , username});
          console.log(this.characters)
          // localStorage.setItem('characters', JSON.stringify(this.characters));
        } else {
